












































var graceChecker = require("../../common/graceChecker.js");
// 18536654578
export default {
	data() {
		return {
			phones: '',
			code: '',
			getCode: '',
			password: '',
			confirmPassword: '',
			btntxt: "获取验证码",
			time: 0,
			disabled: false,
		}
	},
	methods: {
		//点击注册
		submit(e) {
			console.log(uni.getStorageSync('referee'));
			// 进行手机号码的验证
			var rule = [{
					name: "phones",
					checkType: "phoneno",
					checkRule: "11",
					errorMsg: "请输入正确手机号格式"
				},
				{
					name: "password",
					checkType: "password",
					checkRule: "6,8",
					errorMsg:"请输入包含数字和英文字母的密码或密码长度大于6位"
				},
			];
			//进行表单检查
			// console.log(e);
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			console.log(checkRes);
			//验证验证码是否正确
			if (this.code != this.getCode) {
				uni.showToast({
					title: '验证码输入错误',
					icon: 'none'
				})
				return false;
			}
			// 验证---密码和确认密码是否一致
			// console.log(this.password);
			// console.log(this.confirmPassword);
			if (this.password != this.confirmPassword) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none'
				})
				return false;
			}
			// 如果核对信息填写全部正确 存储用户信息
			if (checkRes) {
				uni.request({
					url: this.apiUrl + 'login/forgetPassword',
					method: 'POST',
					data: {
						mobile: this.phones,
						password: this.password,
						confirm_password: this.confirmPassword
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						console.log(res);
						if (res.data.code == 1) {
							uni.showToast({
								title: "您还没有注册",
								icon: "none",
								success: (res) => {
									setTimeout(function() {
										uni.redirectTo({
											url: '../register/register',
										})
									}, 500);
								}
							});
							return;
						}
						if (res.data.code == 0) {
							uni.showToast({
								title: "修改成功",
								icon: "none",
								success: (res) => {
									uni.redirectTo({
										url: '../login/login',
									})
								}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					// title: '请输入正确信息',
					icon: "none"
				});
			}
		},
		// 点击获取验证码按钮
		Getcode() {
			this.time = 60;
			// this.disabled = true;
			uni.request({
				url: this.apiUrl + 'user/getCode',
				method: 'GET',
				data: {
					mobile: this.phones
				},
				success: (res) => {
					console.log(res);
					if (res.data.code == 0) {
						uni.showToast({
							title: "已发送，请注意查收信息",
							icon: "none"
						});
						// 获取发送至手机的验证码
						console.log(res.data.data.code);
						// 获取发送验证码赋值 getCode
						this.getCode = res.data.data.code;
						
						this.disabled =true;
						this.timer();
						
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		
		},
		timer :function() {
			if (this.time > 0) {
				this.time--;
				this.btntxt = this.time + "S后获取";
				setTimeout(this.timer, 1000);
				
			} else {
				this.time = 0;
				this.btntxt = "获取验证码";
				this.disabled =false;
			}
		}
	}
}
